










































































































































































































@import './styles/colors';

a {
  color: white;
  text-decoration: none;
}

.oslo__content {
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  max-height: 100%;
  padding-left: 300px;
  position: relative;
}

.oslo__logo {
  cursor: pointer;
  margin-top: 3rem;
  margin-bottom: 2rem;
}

.oslo__navigation-drawer {
  background-color: $color-purple;
  display: flex;
  flex-direction: column;
}

.oslo__navigation-link {
  $p: &;

  background-color: $color-purple;
  color: rgba(white, 0.9);
  transition: all 0.3s ease-in-out;

  &--active {
    background-color: $color-blue;
    transition: all 0.3s ease-in-out;

    #{$p}--label {
      color: $color-purple;
      font-weight: 500;
    }

    input[type='checkbox']:checked + label {
      opacity: 0;
    }
  }

  &--compare {
    background-color: lighten($color-purple, 5%);
    transition: background-color 0.3s ease-in-out;
  }

  &--label {
    letter-spacing: 0.3px;
  }
}

.oslo__navigation-topbar {
  background-color: white;
  border-bottom: 1px solid $color-grey-300;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 100%;
}

.oslo__topbar {
  color: $color-purple;
  font-weight: bold;
  margin-bottom: 2rem;

  &-text {
    letter-spacing: 0.8px;
    margin-left: 1rem;
    margin-top: 0.3rem;
  }
}

.v-input--selection-controls {
  margin-top: 0.6rem;
  padding-top: 0;
}

.v-select__selection--uppercase {
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

input[type='checkbox'] {
  display: none;

  & + label {
    cursor: pointer;
    display: inline-block;
    height: 40px;
    opacity: 0.5;
    position: relative;
    vertical-align: middle;
    width: 50px;
    padding-left: 1rem;

    // checked inside border
    &::after {
      background-color: $color-blue;
      border-radius: 1px;
      content: '';
      height: 10px;
      left: 24px;
      position: absolute;
      top: 14px;
      width: 10px;
      transform: scale(0);
      transition: all 0.3s ease-in-out;
    }

    // unchecked border
    &::before {
      -moz-border-radius: 1px;
      -webkit-border-radius: 1px;
      border: 1px solid white;
      border-radius: 1px;
      content: '';
      height: 18px;
      left: 20px;
      opacity: 0.2;
      position: absolute;
      top: 10px;
      width: 18px;
      transition: all 0.3s ease-in-out;
    }
  }

  &:checked + label {
    position: relative;
    opacity: 1;

    // checked inside border
    &::after {
      transform: scale(1);
      transition: transform 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }

    // checked border
    &::before {
      border: 1px solid $color-blue;
      opacity: 1;
      transition: all 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }
  }
}
